import React, { Component } from "react";
import { ThunkDispatch } from "redux-thunk";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {
  createStyles,
  Grid,
  Theme,
  Typography,
  WithStyles,
  withStyles,
  CircularProgress,
} from "@material-ui/core";

import { requestLesson } from "../../../_actions/lesson.actions";
import {
  requestPlayer,
  resetPlayer,
  changeMode,
} from "../../../_actions/player.actions";
import { pathFinder } from "../../../_helpers/constants";
import { ICourse } from "../../../_types/course.types";
import { AppActions, AppState } from "../../../_types";
import { ILesson } from "../../../_types/lesson.types";
import {
  PlayerLeftSide,
  PlayerRightSide,
} from "../../Layout/Player/PlayerSides";
import { IMelodition } from "../../../_types/player.types";
import Fingers from "../../Layout/Instruments/Finger/Fingers";
import Piano from "../../Layout/Instruments/Piano/Piano";
import { PlayButton } from "../../Layout/Player/Buttons";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "100vw",
      minHeight: "calc(100vh - 128px)",
      overflow: "hidden",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 113px)",
      },
      display: "flex",
    },
    autoMargin: {
      margin: "auto",
    },
    main: {
      width: "100vw",
      minHeight: "calc(100vh - 128px)",
      overflow: "hidden",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 113px)",
      },
      display: "flex",
    },
    content: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
    doubleGrid: {
      flexWrap: "nowrap",
      width: "100%",
      maxWidth: "100%",
      "& > div": {
        margin: "auto",
        padding: 0,
        height: "100%",
      },
    },
    centerText: {
      textAlign: "center",
    },
  });

interface LessonProps extends WithStyles<typeof useStyles> {
  error: string;
  data?: ILesson;
  course?: ICourse;
  pathname: string;
  melodition: IMelodition;
  theme: "light" | "dark";
}

interface States {}

type Props = LessonProps & ReturnType<typeof mapDispatchToProps>;

class Lesson extends Component<Props, States> {
  shouldComponentUpdate(nextProps: Props) {
    if (nextProps.data && this.props.data?.id !== nextProps.data.id) {
      this.changeContent(nextProps.data.content);
    }
    return true;
  }
  componentDidMount() {
    console.log("mounted");
    const path = pathFinder(this.props.pathname);
    this.props.requestLesson(path);
    if (this.props.data?.id === path.lesson) {
      this.changeContent(this.props.data!.content);
    }
  }
  componentWillUnmount() {
    console.log("dismount");
    this.props.resetPlayer();
    this.setState = () => {
      return;
    };
  }

  changeContent = (fullContent: string) => {
    const content = JSON.parse(fullContent);
    this.props.requestPlayer(content);
  };

  svgWidth = (i: number): number => {
    const num = i === 1 ? 668 : 584;
    return num;
  };

  playModeChanger = () => {
    this.props.changeMode(true);
  };
  render() {
    const { theme, classes, data, melodition, error, ...rest } = this.props;
    return (
      <div className={classes.root}>
        {melodition.loading || melodition.playMode ? (
          <CircularProgress color="secondary" className={classes.autoMargin} />
        ) : data ? (
          <Grid container className={classes.main}>
            <Grid item xs={1}>
              <PlayerLeftSide {...rest} data={data} />
            </Grid>
            <Grid item xs={10}>
              <Grid
                container
                direction="column"
                justify="center"
                alignItems="center"
                className={classes.content}
              >
                <Grid item xs={3} className={classes.doubleGrid}>
                  <Grid container justify="center" alignItems="center">
                    <Grid item xs={9}>
                      <Typography variant="h4">{data.title}</Typography>
                    </Grid>
                    <Grid item xs={3} className={classes.centerText}>
                      <PlayButton loading={melodition.loading} />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={6} className={classes.doubleGrid}>
                  <Grid container justify="center" alignItems="center">
                    <Grid item xs={12}>
                      <Piano />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={3} className={classes.doubleGrid}>
                  <Grid container justify="center" alignItems="center">
                    <Grid item xs={6}>
                      <Typography>{data.excerpt}</Typography>
                    </Grid>
                    <Grid item xs={6} className={classes.centerText}>
                      <Fingers />
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={1}>
              <PlayerRightSide playModeChanger={this.playModeChanger} />
            </Grid>
          </Grid>
        ) : (
          <Typography component="div" className={classes.autoMargin}>
            {error}
          </Typography>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state: AppState, ownProps: Props) => ({
  ...state.single.lesson,
  melodition: state.melodition,
  theme: state.auth.theme,
  classes: ownProps.classes,
  course: state.single.course.data,
  pathname: state.router.location.pathname,
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  changeMode: bindActionCreators(changeMode, dispatch),
  requestLesson: bindActionCreators(requestLesson, dispatch),
  requestPlayer: bindActionCreators(requestPlayer, dispatch),
  resetPlayer: bindActionCreators(resetPlayer, dispatch),
});

const connectedLesson = connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(Lesson));

export default connectedLesson;
