import React, { useState } from "react";
import {
  Drawer,
  Grid,
  IconButton,
  makeStyles,
  createStyles,
  Theme,
} from "@material-ui/core";
import { ArrowBackIos, FormatListNumberedRtl } from "@material-ui/icons";
import { PathFinder } from "../../../_helpers/constants";
import { ICourse } from "../../../_types/course.types";
import { ILesson } from "../../../_types/lesson.types";
import SongListSelector from "../../LearningPages/Lesson/SongListSelector";
import Seekbar from "./SeekBar";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    playMain: {
      minHeight: "calc(100vh - 64px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 56px)",
      },
    },
    content: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
    doubleGrid: {
      flexWrap: "nowrap",
      width: "100%",
      maxWidth: "100%",
      "& > div": {
        margin: "auto",
        padding: 0,
        height: "100%",
      },
    },
    centerText: {
      textAlign: "center",
    },
    sideStyle: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
  })
);

interface RightSideProps {
  playModeChanger: () => void;
}

export const PlayerRightSide: React.FC<RightSideProps> = ({
  playModeChanger,
}) => {
  const classes = useStyles();
  return (
    <Grid
      container
      direction="column"
      justify="center"
      alignItems="center"
      className={classes.sideStyle}
    >
      <IconButton
        color="secondary"
        aria-label="play mode"
        onClick={playModeChanger}
      >
        <ArrowBackIos />
      </IconButton>
    </Grid>
  );
};

interface LeftSideProps {
  data: ILesson;
  course?: ICourse;
  pathname: string;
  requestLesson: (path: PathFinder) => void;
}

export const PlayerLeftSide: React.FC<LeftSideProps> = (props) => {
  const [drawer, setDrawer] = useState(false);

  const toggleDrawer = (
    event: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    if (event.type === "keydown") {
      return;
    }
    if (drawer) {
      setDrawer(false);
    } else {
      setDrawer(true);
    }
  };

  const { data, ...rest } = props;

  const disableList = props.course ? false : true;
  const classes = useStyles();

  return (
    <Grid
      container
      direction="column"
      justify="space-around"
      alignItems="center"
      className={classes.sideStyle}
    >
      <div style={{ height: "40%" }}>
        <Seekbar />
      </div>
      <>
        <IconButton
          color="secondary"
          aria-label="play mode"
          onClick={(e) => toggleDrawer(e)}
          disabled={disableList}
        >
          <FormatListNumberedRtl />
        </IconButton>
        <Drawer anchor="left" open={drawer} onClose={() => setDrawer(false)}>
          <SongListSelector {...rest} lessonId={data.id} />
        </Drawer>
      </>
    </Grid>
  );
};
